<template>
  <v-container>
    <v-row>
      <v-col cols="12" class="text-center">
        <h5>{{ image_number }} / {{ number_of_images }}</h5>
      </v-col>
    </v-row>

    <v-row>
      <v-col cols="12" md="6">

        <!-- Display the image -->
        <div class="image-container">
          <!-- Display loading spinner or placeholder -->
          <div v-if="imageLoading" class="loading-spinner">
            <v-progress-circular indeterminate color="primary"></v-progress-circular>
          </div>
          <img
              v-if="everything_completed"
              class="image-completed"
              src="@/assets/slide_completed.png"
              alt="Completed Slide"
          />
          <!-- Display image when not loading -->
          <img
              v-else-if="not_completed && !imageLoading"
              class="image-rotate"
              :src="raw_image"
              alt="Image"
              :style="{ transform: `rotate(${rotation}deg)` }"
          />
        </div>

      </v-col>

      <v-col cols="12" md="6">
        <!-- Side Panel for Selection -->
        <v-card class="side-panel">
          <!--          <v-card-title class="text-center">Embryo Information</v-card-title>-->
          <!--          <v-divider></v-divider>-->

          <!-- EmbryoCategory Selection -->
          <h5>Embryo Category</h5>
          <v-chip-group class="chip-group">
            <v-chip
              v-for="(option, index) in EmbryoCategoryOptions"
              :key="index"
              :input-value="selectedEmbryoCategory === option"
              @click="toggleEmbryoCategory(option)"
              :style="getChipStyle(selectedEmbryoCategory === option)"
            >
              {{ option }}
            </v-chip>
          </v-chip-group>

          <v-divider></v-divider>

          <!-- ICM Grade Selection -->
          <h4>ICM Grade</h4>
          <v-chip-group class="chip-group">
            <v-chip
              v-for="(option, index) in ICMGradeOptions"
              :key="index"
              :disabled="disableTrophectodermICM"
              :input-value="selectedICMGrade === option"
              @click="toggleICMGrade(option)"
              :style="getChipStyle(selectedICMGrade === option)"
            >
              {{ option }}
            </v-chip>
          </v-chip-group>

          <v-divider></v-divider>

          <!-- Trophectoderm Grade Selection -->
          <h4>Trophectoderm Grade</h4>
          <v-chip-group class="chip-group">
            <v-chip
              v-for="(option, index) in TrophectodermGradeOptions"
              :key="index"
              :disabled="disableTrophectodermICM"
              :input-value="selectedTrophectodermGrade === option"
              @click="toggleTrophectodermGrade(option)"
              :style="getChipStyle(selectedTrophectodermGrade === option)"
            >
              {{ option }}
            </v-chip>
          </v-chip-group>

          <v-divider></v-divider>

          <!-- Action Buttons -->
          <v-card-actions class="justify-center">
            <!-- Add the "Unable to Annotate" Button -->
            <v-card-actions class="justify-center">
              <v-btn color="error" @click="flag">
                <v-icon left>mdi-flag</v-icon>
                Flag
              </v-btn>
            </v-card-actions>

            <v-btn color="secondary" @click="clearForm">CLEAR</v-btn>
            <v-btn
              :loading="loading"
              :disabled="disableSubmit()"
              color="primary"
              @click="submit"
            >
              SUBMIT
              <template v-slot:loader>
                <v-icon light>mdi-cached</v-icon>
              </template>
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
// import * as Buffer from "vuetify";
import colors from "vuetify/es5/util/colors";

import store from "@/store";

const axios = require("axios");

export default {
  data: () => ({
    ICMGradeOptions: ["A", "B", "C", "D"],

    TrophectodermGradeOptions: ["A", "B", "C", "D"],
    EmbryoCategoryOptions: [1, 2, 3, 4, 5, 6, "M-A", "M-B", "D"],

    selectedICMGrade: null,
    selectedTrophectodermGrade: null,
    selectedEmbryoCategory: null,
    loader: null,
    loading: false,

    submit_button_disable: true,

    cors: "https://cors-anywhere.herokuapp.com/",
    raw_image: null,

    label: "",
    imageno: "",
    // slide: "",
    image_name: "",
    image_number: "",
    number_of_images: "",
    user_id: "",
    everything_completed: false,
    not_completed: false,
    rotation: 0,
    flag_val: true,
    imageLoading: false,

  }),

  watch: {
    image_name: function() {
      if (this.image_name) this.get_image();
    },

    loader() {
      const l = this.loader;
      this[l] = !this[l];

      setTimeout(() => (this[l] = false), 3000);

      this.loader = null;
    },
    disableTrophectodermICM: function() {
      if (this.disableTrophectodermICM) {
        this.selectedICMGrade = null;
        this.selectedTrophectodermGrade = null;
      }
    }
  },
  computed: {
    get_user() {
      return store.state.userid;
    },

    disableTrophectodermICM: function() {
      return (
        this.selectedEmbryoCategory === "M-A" ||
        this.selectedEmbryoCategory === "M-B" ||
        this.selectedEmbryoCategory === "D" ||
        this.selectedEmbryoCategory === 1
      );
    },

    selectedChipBackgroundColor() {
      return colors.blue.darken2;
    },

    size() {
      const size = { xs: "small", sm: "small", lg: "large", xl: "x-large" }[
        this.$vuetify.breakpoint.name
      ];
      return size ? { [size]: true } : {};
    }
  },

  created() {
    this.clearForm();
    this.userid = store.state.userid;

    // console.log("userid: ", this.userid);
    if (this.userid == null) {
      this.$router.push({ path: "/login" });
    }

    this.username = store.state.username;
    this.image_name = this.getimagenumber();

    // console.log("Username: ", this.username);
    // console.log("userid: ", this.userid);
    // console.log("image_name: ", this.image_name);
    // console.log("slide_id: ", this.slide_id);

    // console.log("size: ", this.$vuetify.breakpoint.name);

    if (
      this.$vuetify.breakpoint.name === "xs" ||
      this.$vuetify.breakpoint.name === "sm"
    ) {
      this.rotation -= 0;
    }
    this.submit_button_disable = this.disableSubmit();

    // this.reset_ann()
  },
  rotateRight() {},
  methods: {
    get_model: function(name) {
      return name.toLowerCase().replace(" ", "_");
    },
    toggleICMGrade(option) {
      this.selectedICMGrade = this.selectedICMGrade === option ? null : option;
    },
    toggleTrophectodermGrade(option) {
      this.selectedTrophectodermGrade =
        this.selectedTrophectodermGrade === option ? null : option;
    },
    toggleEmbryoCategory(option) {
      this.selectedEmbryoCategory =
        this.selectedEmbryoCategory === option ? null : option;
    },

    getChipStyle(selected) {
      if (selected) {
        return {
          backgroundColor: colors.blue.darken2,
          color: "white",
          font: "50px" /* You can adjust the font size */,
          padding: "20px 16px" /* You can adjust the padding */
        };
      } else {
        return {
          backgroundColor: "",
          color: "",
          font: "20px" /* You can adjust the font size */,
          padding: "20px 16px" /* You can adjust the padding */
        };
      }
    },

    clearForm() {
      this.selectedICMGrade = null;
      this.selectedTrophectodermGrade = null;
      this.selectedEmbryoCategory = null;
      this.submit_button_disable = true;
    },

    disableSubmit: function() {
      // Check if all necessary selections are made
      return (
        !this.selectedEmbryoCategory ||
        (!this.selectedTrophectodermGrade && !this.disableTrophectodermICM) ||
        (!this.selectedICMGrade && !this.disableTrophectodermICM)
      );
    },

    flag: function() {
      this.flag_val = true;
      this.submit();
    },
    submit: function() {
      this.loader = "loading";
      this.loading = !this.submit_button_disable;
      let label;

      label =
        this.selectedEmbryoCategory +
        "_" +
        this.selectedICMGrade +
        "_" +
        this.selectedTrophectodermGrade;
      let comment = "";
      if (this.flag_val) {
        label = "-1";
        comment = "unable to annotate";
      }
      console.log(label);

      let data = {
        username: this.userid,
        image_name: this.image_name,
        label: label,
        comment: comment
      };

      const get_label = this.$backendhostname + "/give_label";

      axios
        .post(get_label, data, {
          headers: {
            "Content-Type": "application/json",
            "Access-Control-Allow-Origin": "*",
            "Access-Control-Allow-Headers":
              "Origin, X-Requested-With, Content-Type, Accept"
          }
        })

        .then(response => {
          if (response.data.response) {
            // console.log(response.data.response);
            this.raw_image = "";
            this.image_name = this.getimagenumber();
            this.clearForm();
          }

          // var res = response.data.user

          // dispatch({type: FOUND_USER, data: response.data[0]})
        })
        .catch(error => {
          this.resp = error;
          console.error(error);

          // dispatch({type: ERROR_FINDING_USER})
        });
    },

    give_label: function(temp) {
      // console.log(temp);

      let data = {
        username: this.userid,
        image_name: this.image_name,
        label: temp - 1,
        comment: ""
      };

      const get_label = this.$backendhostname + "/give_label";

      axios
        .post(get_label, data, {
          headers: {
            "Content-Type": "application/json",
            "Access-Control-Allow-Origin": "*",
            "Access-Control-Allow-Headers":
              "Origin, X-Requested-With, Content-Type, Accept"
          }
        })

        .then(response => {
          if (response.data.response) {
            // console.log("success");
            this.raw_image = "";
            this.image_name = this.getimagenumber();
          }
          // var res = response.data.user

          // dispatch({type: FOUND_USER, data: response.data[0]})
        })
        .catch(error => {
          this.resp = error;
          console.error(error);

          // dispatch({type: ERROR_FINDING_USER})
        });
    },
    getimagenumber: function() {
      // console.log(this.user_id);
      this.imageLoading = true;
      let data = {
        username: this.userid
        // slide: this.slide_id
      };

      const get_image_number_url = this.$backendhostname + "/get_image_number";

      // this.$route.params

      axios
        .post(get_image_number_url, data, {
          headers: {
            "Content-Type": "application/json",
            "Access-Control-Allow-Origin": "*",
            "Access-Control-Allow-Headers":
              "Origin, X-Requested-With, Content-Type, Accept"
          }
        })

        .then(response => {
          if (response.data.response === "success") {
            // console.log("success");
            this.not_completed = true;
            this.batch_completed = false;
            this.everything_completed = false;

            this.image_name = response.data.image_name;
            this.image_number = response.data.image_number;
            this.number_of_images = response.data.number_of_images;

            return response.data.image_name;
          } else if (response.data.response === "completed") {
            this.batch_completed = false;
            this.not_completed = false;
            this.everything_completed = true;
          }
          this.imageLoading = false;

          // var res = response.data.user

          // dispatch({type: FOUND_USER, data: response.data[0]})
        })
        .catch(error => {
          this.resp = error;
          console.error(error);
          this.imageLoading = false;

          // dispatch({type: ERROR_FINDING_USER})
        });
    },

    get_image: function() {
      this.imageLoading = true; // Set loading to true
      let data = {
        image_name: this.image_name
      };

      const get_image_url = this.$backendhostname + "/get_image";

      axios
          .post(get_image_url, data, {
            responseType: "arraybuffer",
            headers: {
              "Content-Type": "application/json",
              "Access-Control-Allow-Origin": "*",
              "Access-Control-Allow-Headers":
                  "Origin, X-Requested-With, Content-Type, Accept"
            }
          })

          .then(response => {
            this.raw_image = this._imageEncode(response.data);
            this.imageLoading = false; // Set loading to false when image is loaded
          })
          .catch(error => {
            this.resp = error;
            console.error(error);
            this.imageLoading = false; // Set loading to false in case of an error
          });
    },

    _imageEncode: function(arrayBuffer) {
      // let u8 = new Uint8Array(arrayBuffer);
      let b64encoded = btoa(
        [].reduce.call(
          new Uint8Array(arrayBuffer),
          function(p, c) {
            return p + String.fromCharCode(c);
          },
          ""
        )
      );
      let mimetype = "image/png";
      return "data:" + mimetype + ";base64," + b64encoded;
    }
  }
};
</script>

